*,
*:before,
*:after {
 margin: 0;
 padding: 0;
 box-sizing: border-box;
}

h2 {
 font-size: 50px;
 font-weight: bold;
}

ul {
 list-style: none !important;
 margin: 0 !important;
 padding: 0 !important;
}

a {
 text-decoration: none;
 margin: 0 !important;
 padding: 0 !important;
}
