header {
 .header-top {
  background-color: $mainColor;
  padding: 13.5px 0;
 }

 .header-top a {
  color: #fff;
 }

 .contact-details {
  line-height: 1.2;

  .contact-details__list-item:first-child {
   margin-left: 80px;
  }
 }

 .socialmedia {
  .socialmedia__list-item {
   width: 30px;
   height: 30px;
   line-height: 30px;
   border: 1px solid #fff;
   border-radius: 50%;
   text-align: center;
   margin-#{$end}: 8px;
  }
 }

 .navbar {
  .nav-item:not(:last-child) {
   margin-#{$end}: 68px;
  }
  .nav-link.active {
   color: $mainColor !important;
  }

  .nav-link {
   font-size: 18px;
  }

  .navbar-nav {
   padding: 0 30px !important;
  }
 }
}
